import { Component, OnInit, ViewChild, ElementRef, HostListener, ViewEncapsulation } from '@angular/core';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { DocumentService } from 'src/app/api/controllers/Document';
import { FileUploadService } from 'src/app/shared/services/file-upload.service';
import { FileDownloadService } from 'src/app/shared/services/file-download.service';
import { DomSanitizer } from '@angular/platform-browser';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { MatDialog } from '@angular/material/dialog';
import { FileDetailsService } from 'src/app/api/controllers/FileDetails';
import { AddPropertyDialogComponent } from 'src/app/modules/ams/profile-settings/components/add-property-dialog/add-property-dialog.component';
import { SessionService } from 'src/app/core/session/session.service';
import { PropertyManagerService } from 'src/app/api/controllers/PropertyManager';
import { MatDrawer } from '@angular/material/sidenav';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as moment from 'moment';
import _moment from "moment-timezone";

declare var d3: any;
declare var GraphCreator: any;
var graph;

@Component({
  selector: 'app-property-view-graphic',
  templateUrl: './property-view-graphic.component.html',
  styleUrls: ['./property-view-graphic.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PropertyViewGraphicComponent implements OnInit {

  loading: boolean = true;
  isShowBuilder: boolean = false;
  savePFNode: any;
  bNo: any = 0;
  fNo: any = 0;
  public towerList: Array<any> = [];
  public floorList: Array<any> = [];
  public floorPropertyData: any;
  public updatedGraph: any;
  public selectedTower: any;
  public selectedFloor: any;
  public selectedFloorLabel: any;
  public selectedFloorNo: any;
  public uploadResponse: any;
  public onChangeVal: any;
  public currentPOIId: any;
  public propertyTypeList: Array<any> = [];
  public unitFilterList: Array<any> = [];
  public selectedUnitFilterValue: any;
  public selectedPropertyType: any;
  public styleTop: any;
  public styleLeft: any;
  public isShowOver = 'none';
  public floorMapJson: any;
  @ViewChild('poiId') poiId: ElementRef;
  @ViewChild('nodeId') nodeId: ElementRef;
  @ViewChild('nodeKind') nodeKind: ElementRef;
  @ViewChild('type') Nodetype: ElementRef;
  @ViewChild('matDrawer', { static: true }) matDrawer: MatDrawer;
  constructor(private element: ElementRef,
    private apartmentService: ApartmentService,
    private sharedService: SharedService,
    private fileUploadService: FileUploadService,
    private fileDownloadService: FileDownloadService,
    private sanitizer: DomSanitizer,
    private lookupService: LookupService,
    private propertyManagerService: PropertyManagerService,
    public dialog: MatDialog,
    private sessionService: SessionService,
    private fileDetailService: FileDetailsService) {
    localStorage.setItem('preventGraph', 'true');
  }

  ngOnInit(): void {
    this.getBlockList();
    this.getPropertyType();
    this.unitFilterList = [
      {
        id: 'isAvailableForRent',
        name: "Available For Rent"
      },
      {
        id: 'isPetsAllowed',
        name: "Pets Allowed"
      },
      {
        id: 'isFurnished',
        name: "Furnished"
      },
      {
        id: 'isVacant',
        name: "Vacant"
      },
      {
        id: 'isSold',
        name: "Sold"
      },
      {
        id: 'constructionInProgress',
        name: "Construction In Progress"
      },
      {
        id: 'twoBR',
        name: "2 BR"
      }
    ]
  }
    
  ngAfterViewInit(){
    this.bootMe();
  }


  selectPropertyType(e) {
    this.selectedPropertyType = e[0].lookupValueId;
    switch (Number(this.selectedPropertyType)) {
      case 318:
        this.getUnitProperty();
        break;
      case 319:
        this.getAssetProperty();
        break;
      case 320:
        this.getFacilityProperty();
        break;
      case 321:
        this.getMaintananceProperty();
        break;
      default:
        break;
    }
  }

  getUnitProperty() {
    let getParam = {
      apartmentId: this.sessionService.apartmentId,
      propertyPOIType: this.selectedPropertyType
    }
    this.propertyManagerService.getAllPropertyPoiUnitByPropertyPoiType(getParam).subscribe(
      resp => {

      }
    )
  }

  getAssetProperty() {
    let getParam = {
      apartmentId: this.sessionService.apartmentId,
      propertyPOIType: this.selectedPropertyType
    }
    this.propertyManagerService.getAllPropertyPoiAssetByPropertyPoiType(getParam).subscribe(
      resp => {

      }
    )
  }

  getFacilityProperty() {
    let getParam = {
      apartmentId: this.sessionService.apartmentId,
      propertyPOIType: this.selectedPropertyType
    }
    this.propertyManagerService.getAllPropertyPoiFacilityByPropertyPoiType(getParam).subscribe(
      resp => {

      }
    )
  }

  getMaintananceProperty() {
    let getParam = {
      apartmentId: this.sessionService.apartmentId,
      propertyPOIType: this.selectedPropertyType
    }
    this.propertyManagerService.getAllPropertyPoiOpMaintenanceByPropertyPoiType(getParam).subscribe(
      resp => {

      }
    )
  }

  getBlockList() {
    let param = {
      apartmentId: this.sessionService.apartmentId
    }
    this.apartmentService.getApartmentBlockByApartmentId(param).subscribe(
      (resp: any) => {
        this.towerList = resp;
        this.selectedTower = this.towerList[0].apartmentBlockId;
        this.getFloorList(undefined);
      },
      error => {

      }
    )
  }

  getFloorList(e) {
    if (e) {
      this.selectedTower = e[0].apartmentBlockId;
    }
    let getFloorParam = {
      apartmentId: this.sessionService.apartmentId,
      apartmentblockId: this.selectedTower
    }
    this.apartmentService.getApartmentBlockFloorByApartmentIdBlockId(getFloorParam).subscribe(
      (resp: any) => {
        if (resp && resp.length) {
          this.floorList = resp;
          this.selectedFloor = this.floorList[0].apartmentBlockFloorId;
          this.selectFloor(undefined);
          // this.getPropertyMap(this.floorList[0].propertyMapFileId);
        }
      },
      error => {

      }
    )
  }

  selectFloor(e) {
    if (e) {
      this.selectedFloor = e[0].apartmentBlockFloorId;
    }
    if (this.selectedFloor) {
      this.floorList.filter(key => {
        if (key.apartmentBlockFloorId == this.selectedFloor) {
          this.selectedFloorLabel = key.floorLabel;
          this.selectedFloorNo = key.floorno;
          this.getPropertyMap(key.propertyMapFileId);
        }
      })
    }
  }


  getPropertyType() {
    let getLookUpParam = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 196
    }
    this.lookupService.getLookupValueByLookupTypeId(getLookUpParam).subscribe(
      (resp: any) => {
        this.propertyTypeList = resp;
      },
      error => {

      }
    )
  }

  addProperty(type): void {
    let data: any = this.onChangeVal;
    data['type'] = type;
    data['apartmentBlockId'] = this.selectedFloor;
    const dialogRef = this.dialog.open(AddPropertyDialogComponent, {
      width: '20%',
      position: {
        right: '10%'
      },
      data: data
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        let objectVal: Array<string> = [];
        for (const key in result) {
          objectVal.push(key)
        }
        objectVal.splice(0, 2);
        // let arrObjVal = JSON.stringify(objectVal);
        $('#nodeKind').val(objectVal.toString());
        $('#spanNode').val(this.onChangeVal.nodeId);
        $('#poiId').val(result.poiId);
        $('#type').val(result.type);
        this.updatePOI();
      }
    });
  }


  getPropertyMap(propertyId) {
    let getFloorProperty = {
      propertyMapFileId: propertyId
    }
    this.apartmentService.getPropertyMapFileById(getFloorProperty).subscribe(
      (resp: any) => {
        if (resp && resp.length) {
          this.floorPropertyData = resp[0];
          this.assignData(this.floorPropertyData)
        }
      },
      error => {

      }
    )
  }

  assignData(data) {
    if (data && data.floorPictureFileDetailsId && data.floorPictureFileDetailsId != 0 && data.mapJsonFile != "") {
      this.isShowBuilder = true;
      setTimeout(() => {
        this.removeD3Elements();
        graph.deleteGraph();
        this.getFileDetails(data.floorPictureFileDetailsId);
        graph.customGraphUpdate(data.mapJsonFile, undefined);
        this.floorMapJson = data.mapJsonFile;
      }, 1)
    } else if (data && data.floorPictureFileDetailsId && data.floorPictureFileDetailsId != 0) {
      this.isShowBuilder = true;
      setTimeout(() => {
        this.removeD3Elements();
        this.getFileDetails(data.floorPictureFileDetailsId);
        // graph.customImageUpdalod(this.getImage(data.floorPictureFileDetailsId));
      }, 1)
    } else {
      this.isShowBuilder = false;
    }
  }

  getFileDetails(imageId) {
    let newParams = {
      fileDetailsId: imageId,
      apartmentId: this.sessionService.apartmentId
    }

    this.fileDetailService.getFileDetailsById(newParams).subscribe((res: any) => {
      this.downloadFile(res[0].filePath)
    })
  }

  downloadFile(filePath) {
    this.fileDownloadService.downloadFile(filePath).subscribe((res: any) => {
      const blob = res.body
      let objectURL = URL.createObjectURL(blob);
      let sanitizeUrl: any = this.sanitizer.bypassSecurityTrustUrl(objectURL);
      graph.customImageUpdalod(sanitizeUrl.changingThisBreaksApplicationSecurity);
    })

  }

  getImage(floorPictureFileDetailsId) {
    let downloadParam = {
      fileNameOnBlob: floorPictureFileDetailsId,
      savefileNameWithPath: ''
    }
    this.fileDetailService.downloadAsync(downloadParam).subscribe(
      resp => {
        return resp;
      },
      error => {

      }
    )
  }

  bootMe() {
    let docEl = document.documentElement,
      bodyEl = document.getElementsByTagName('body')[0];

    let width = window.innerWidth || docEl.clientWidth || bodyEl.clientWidth,
      height = window.innerHeight || docEl.clientHeight || bodyEl.clientHeight;
    // initial node data
    var nodes = [];
    var edges = [];
    /** MAIN SVG **/
    var svg = d3.select("#lcanvas").append("svg")
      .attr("width", width)
      .attr("height", height);
    graph = new GraphCreator(svg, nodes, edges);
    graph.setIdCt(0);
    // this.graph.updateGraph();
  }

  addPropertyMap(imageId) {
    this.downloadGraph();
    let addPropertyMap = {
      propertyMapFile: {
        "propertyMapFileId": 0,
        "apartmentId": this.sessionService.apartmentId,
        "apartmentBlockId": this.selectedTower,
        "description": "",
        "isIndoor": true,
        "floorno": this.selectedFloorNo,
        "floorLabel": this.selectedFloorLabel,
        "floorPictureFileDetailsId": imageId,
        "mapJsonFile": JSON.stringify(this.updatedGraph),
        "isActive": true,
        "insertedBy": this.sessionService.userId,
        "insertedOn": _moment().toISOString(),
        "updatedBy": 0,
        "updatedOn": _moment().toISOString(),
        "apartmentBlockFloorId": this.selectedFloor
      }
    }
    this.apartmentService.addPropertyMapFile(addPropertyMap).subscribe(
      (resp: any) => {
        this.floorPropertyData = resp;
        this.assignData(this.floorPropertyData)
      },
      error => {

      }
    )
  }

  updateProperty() {
    this.downloadGraph();
    let updatePropertyMap = {
      propertyMapFile: {
        "propertyMapFileId": this.floorPropertyData.propertyMapFileId,
        "apartmentId": this.sessionService.apartmentId,
        "apartmentBlockId": this.selectedFloor,
        "description": "",
        "isIndoor": true,
        "floorno": this.selectedFloorNo,
        "floorLabel": this.selectedFloorLabel,
        "floorPictureFileDetailsId": this.floorPropertyData.floorPictureFileDetailsId,
        "mapJsonFile": JSON.stringify(this.updatedGraph),
        "isActive": true,
        "insertedBy": 0,
        "insertedOn": _moment().toISOString(),
        "updatedBy": this.sessionService.userId,
        "updatedOn": _moment().toISOString(),
        "apartmentBlockFloorId": this.selectedTower
      }
    }
    this.apartmentService.updatePropertyMapFile(updatePropertyMap).subscribe(
      (resp: any) => {
        this.floorPropertyData = resp;
        // this.assignData(this.floorPropertyData)
      },
      error => {

      }
    )
  }

  downloadGraph() {
    var savePFNodes = [];
    var saveEdges = [];
    var savePFEdges = [];

    var prefix = this.bNo + "_" + this.fNo + "_";
    var sid, tid;
    var nobj;
    this.savePFNode = [];
    graph.nodes.forEach(function (val, i) {
      nobj = {}
      nobj.id = prefix + val.id
      nobj.x = val.x
      nobj.y = val.y
      nobj.poiId = val.poiId
      nobj.kind = val.kind
      savePFNodes.push(nobj)
    });
    this.savePFNode = savePFNodes;
    graph.edges.forEach(function (val, i) {
      saveEdges.push({ source: val.source.id, target: val.target.id });
      sid = prefix + val.source.id;
      tid = prefix + val.target.id;
      savePFEdges.push({ source: sid, target: tid });
    });
    var fullObj = {
      maponly: {
        idct: graph.idct,
        buildingNo: this.bNo,
        floorNo: this.fNo,
        nodes: graph.nodes,
        edges: saveEdges
      }, output: {
        floorNo: this.fNo,
        nodes: savePFNodes,
        edges: savePFEdges
      }
    };
    this.updatedGraph = fullObj;
    /*fullObj.maponly.idct = graph.idct
    fullObj.maponly.buildingNo = this.bNo
    fullObj.maponly.floorNo = this.fNo
    fullObj.maponly.nodes = graph.nodes
    fullObj.maponly.edges = saveEdges
    fullObj.output.floorNo = this.fNo
    fullObj.output.nodes = savePFNodes
    fullObj.output.edges = savePFEdges*/

    // var blob = new Blob([JSON.stringify(fullObj)], {type: "text/plain;charset=utf-8"});
    // //for time being commented out
    // saveAs(blob, "mydag.json");
  }

  selectedUnitFilter(e) {
    this.selectedUnitFilterValue = e[0].id;
    let selectedObj = { type: this.selectedPropertyType, selectedCategory: this.selectedUnitFilterValue }
    graph.customGraphUpdate(this.floorPropertyData.mapJsonFile, selectedObj);
  }

  updatePOI() {
    if ($('#poiId').val() && $('#nodeKind').val() && $('#spanNode').val()) {
      graph.setPOIId();
      this.updateProperty();
    }
  }

  browseFile() {
    var fileUpload = $(this.element.nativeElement).find("input[id='hidden-bg-upload']");
    fileUpload.trigger("click");
  }


  ngOnDestroy() {
    this.removeD3Elements();
    graph.deleteGraph();
    localStorage.removeItem('preventGraph');
  }

  removeD3Elements(){
    d3.select("#bgimage").remove();
    d3.select("#smart-path").remove();
    d3.select("#smart-path-navigation").remove();
  }

  goBack() {
    this.matDrawer.close();
  }


}
